
















import { URL_DB_API, URL_IMAGE_UNKNOWN } from "@/lib/urls";
import Component from "vue-class-component";
import Vue from "vue";
import { Prop } from "vue-property-decorator";
import { Card } from "../../../core/src/main";
import YgoPriceView from "@/components/YgoPriceView.vue";

@Component({
    components: {
        YgoPriceView,
    },
})
export default class YgoCard extends Vue {
    @Prop({ required: true })
    public card: Card;

    get imageUrl() {
        return this.card.image.urlSmall ?? URL_IMAGE_UNKNOWN;
    }

    get referenceUrl() {
        return URL_DB_API + encodeURI(this.card.name);
    }

    onDeckCardRightClicked(e: Event) {
        e.preventDefault();
        this.$emit("deck-card-right-click", e);
    }
}
